import api from "@/app/api";
import { useState } from "react";
export const Graph = () => {
  const tickers = ["NVDA", "AAPL", "MSFT"];
  const [tickerPrice, setTickerPrice] = useState();

  const fetchTickerPrice = async (): Promise<void> => {
    try {
      for (const ticker of tickers) {
        const response = await api.get(`price/${ticker}`);
        setTickerPrice(response.data.tickerPrice);
      }
    } catch (error) {
      console.error("Error fetching ticker price", error);
    }
  };

  return <div className="h-50 w-2/3 bg-white"></div>;
};
